<template>
    <div class="container text-left">
        <form class="pl-7 pr-7 form form-group">
            <div class="form-group">
                <label for="btitle">제목</label> <input type="text" class="form-control" 
                v-model="updateQuestion.btitle" placeholder="제목을 입력하세요">
            </div>
            <div class="form-group">
                <label for="bcontent">내용</label>
                <textarea rows="10" style="height: 100%;" class="form-control"
                v-model="updateQuestion.bcontent" placeholder="내용을 입력하세요"></textarea>
            </div>
            <!--footer-->
            <div class="container row justify-content-center"
                style="margin-bottom: 0">
                <!-- <c:if test="${loginInfo.userid eq dto.userid}"> -->
                    <!-- 세션아이디랑 글쓴이가 같을때만 보여줌 -->
                    <input type="button" class="btn btn-outline-primary mr-1"
                        value="수정하기" @click='modify'>
                    <input type="button" class="btn btn-outline-danger mr-1"
                        value="취소하기" @click='cancle'>
                <!-- </c:if> -->
                <input type="button" class="btn btn-outline-secondary" value="목록"
                    @click='gotoList' >
            </div>
        </form>
    </div>
</template>

<script>
import axios from "axios";

export default {
    data() {
        return {
            updateQuestion: {
                bnum: '',
                btitle: '',
                bcontent: '',
            }
        }
    },
    created() {
        this.updateQuestion = this.$route.params.question;
    },
    methods: {
        modify() {
            console.log("여기입니다" + this.updateQuestion);
            axios.put("http://localhost:7777/happyhouse/qna", this.updateQuestion)
            .then(() => {
                this.$router.push({ name: 'Read', params : { bnum : this.updateQuestion.bnum } });
            })
            .catch((ex) => {console.log(ex)});
        },
        gotoList() {
            this.$router.push("/happyhouse/qna");
        },
        cancle(){
            window.history.back();
        }
    }
}
</script>