import Vue from "vue";
import VueRouter from "vue-router";
import QnaList from "@/views/QnAList.vue";
import QnaRegister from "@/views/QnARegister.vue";
import QnaModify from "@/views/QnAModify.vue";
import QnaRead from "@/views/QnARead.vue";
import BootstrapVue from "bootstrap-vue"
import "bootstrap/dist/css/bootstrap.min.css"
import "bootstrap-vue/dist/bootstrap-vue.css"

Vue.use(BootstrapVue);

Vue.use(VueRouter);

const routes = [
  {
    path: "/happyhouse/qna",
    name: "List",
    component: QnaList,
  },
  {
    path: "/happyhouse/qna/register",
    name: "Register",
    component: QnaRegister
  },
  {
    path: "/happyhouse/qna/modify",
    name: "Modify",
    component: QnaModify
  },
  {
    path: "/happyhouse/qna/:bnum",
    name: "Read",
    component: QnaRead
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
