<script>
import axios from "axios";

export default {
  data(){
    return {
      qnas:[],
      keyword : {
        word : '',
        type : 'btitle'
      }
    }
  },
  created() {
    axios.get("http://localhost:7777/happyhouse/qna/list")
    .then((response) => {
      console.log(response);
      this.qnas = response.data;
    })
    .catch((ex) => {
      console.log(ex);
    })
  },
  methods: {
    search(){
      axios.post("http://localhost:7777/happyhouse/qna/search", this.keyword)
      .then((response)=>{
        this.qnas = response.data;
      })
      .catch((ex) =>{
        console.log(ex);
      })
    },
    gotoRegister(){
      this.$router.push("/happyhouse/qna/register");
    }
  }
};
</script>